.fade-enter-active, .fade-leave-active {
      -webkit-transition: opacity .5s;
      transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
      opacity: 0;
}
    /* 侧滑入效果 */
.slide-fade-enter-active {
      -webkit-transition: all .8s ease;
      transition: all .8s ease;
}
.slide-fade-leave-active {
      -webkit-transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);  /*CSS 贝赛尔曲线*/
}
.slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
      -webkit-transform: translateX(50px);
              transform: translateX(50px);
      opacity: 0;
}
    /* 侧滑入效果 */
.slide2-fade-enter-active {
      -webkit-transition: all .8s ease;
      transition: all .8s ease;
}
.slide2-fade-leave-active {
      -webkit-transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide2-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
      -webkit-transform: translateY(50px);
              transform: translateY(50px);
      opacity: 0;
}
    /* 
        ——————————  时间线样式  ——————————
    */
.timeline {
      list-style: none;
      padding: 20px 0 20px;
      position: relative;
}
.timeline:before {
      top: 0;
      bottom: 0;
      position: absolute;
      content: " ";
      width: 3px;
      background-color: #eeeeee;
      left: 50%;
      margin-left: -1.5px;
}
.timeline > li {
      margin-bottom: 20px;
      position: relative;
}
.timeline > li:before,
    .timeline > li:after {
      content: " ";
      display: table;
}
.timeline > li:after {
      clear: both;
}
.timeline > li:before,
    .timeline > li:after {
      content: " ";
      display: table;
}
.timeline > li:after {
      clear: both;
}
.timeline > li > .timeline-panel {
      width: 46%;
      float: left;
      border: 1px solid #d4d4d4;
      border-radius: 8px;
      padding: 20px;
      position: relative;
      -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
      box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
}
.timeline > li > .timeline-panel:before {
      position: absolute;
      top: 26px;
      right: -15px;
      display: inline-block;
      border-top: 15px solid transparent;
      border-left: 15px solid #ccc;
      border-right: 0 solid #ccc;
      border-bottom: 15px solid transparent;
      content: " ";
}
.timeline > li > .timeline-panel:after {
      position: absolute;
      top: 27px;
      right: -14px;
      display: inline-block;
      border-top: 14px solid transparent;
      border-left: 14px solid #fff;
      border-right: 0 solid #fff;
      border-bottom: 14px solid transparent;
      content: " ";
}
.timeline > li > .timeline-badge {
      color: #fff;
      width: 50px;
      height: 50px;
      line-height: 50px;
      font-size: 1.4em;
      text-align: center;
      position: absolute;
      top: 16px;
      left: 50%;
      margin-left: -25px;
      background-color: #999999;
      z-index: 100;
      border-top-right-radius: 50%;
      border-top-left-radius: 50%;
      border-bottom-right-radius: 50%;
      border-bottom-left-radius: 50%;
}
.timeline > li.timeline-inverted > .timeline-panel {
      float: right;
}
.timeline > li.timeline-inverted > .timeline-panel:before {
      border-left-width: 0;
      border-right-width: 15px;
      left: -15px;
      right: auto;
}
.timeline > li.timeline-inverted > .timeline-panel:after {
      border-left-width: 0;
      border-right-width: 14px;
      left: -14px;
      right: auto;
}
.timeline-badge.primary {
      background-color: #2e6da4 !important;
}
.timeline-badge.success {
      background-color: #3f903f !important;
}
.timeline-badge.warning {
      background-color: #f0ad4e !important;
}
.timeline-badge.danger {
      background-color: #d9534f !important;
}
.timeline-badge.info {
      background-color: #5bc0de !important;
}
.timeline-title {
      margin-top: 0;
      color: inherit;
}
.timeline-body > p,
    .timeline-body > ul {
      margin-bottom: 0;
}
.timeline-body > p + p {
      margin-top: 5px;
}
.img-circle{
      border-radius:0;
}

